.App {
  background-color: #282c34;
  height: 100%;
  /* min-height: 100vh; */
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.carousel {
  height: 95%;
  width: 100%;
}

.carousel-inner,
.carousel-item {
  height: 100%;
}

img {
  margin-right: auto;
  margin-left: auto;
  max-height: 80%;
}

.carousel-control-prev {
  justify-content: flex-start;
  padding-left: 10px;
}

.carousel-control-next {
  justify-content: flex-end;
  padding-right: 10px;
}

.carousel-caption {
  bottom: auto;
  left: 12.5%;
  padding-top: 10px;
  text-align: left;
}

h5 {
  font-weight: 600;
  color: #ff7c7c;
}
